@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: unset;
}

p {
  margin: 0;
}

.appWrapper {
  position: relative;
  background-image: url(/react-weather-api-live/static/media/alberto-restifo-BNHcxk1CDdw-unsplash.f5673112.jpg);
  background-size: cover;
  background-position: center bottom;
  background-repeat: no-repeat;
  padding: 5vw 10vw;
  width: 100%;
  height: 100vh;
  color: #fff;
  z-index: 0;
}

.appWrapper form input {
  max-width: 600px;
  width: 100%;
  height: 50px;
  border-radius: 40px;
  background: transparent;
  border: 2px solid #fff;
  box-shadow: 0 0 10px -2px #fff;
  text-transform: capitalize;
  font-size: 15px;
  padding: 10px 20px;
  font-weight: 600;
  color: #fff;
  letter-spacing: 1px;
}

.appWrapper form {
  text-align: center;
  margin-bottom: 50px;
}

.app-container {
  display: flex;
  height: 80%;
  justify-content: space-between;
  align-items: flex-end;
  text-shadow: 0 0 6px #222;
}

.appWrapper::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  z-index: -1;
}

.overview h1 {
  font-size: 70px;
  font-weight: 600;
}

.overview p {
  font-size: 22px;
  margin-bottom: 20px;
}

.weatherIcon {
  display: inline-block;
  text-align: center;
}

.weatherIcon p {
  margin: 0;
}

.current-temp {
  font-size: 140px;
  margin-bottom: 20px
}

.min-max-temp {
  display: flex;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  font-size: 50px;
}

.min-max-temp > div {
  display: flex;
  flex-direction: column;
  line-height: 1;
}

.min-max-temp span {
  display: inline-block;
  font-size: 16px;
}

@media (max-width: 768px) {
  .appWrapper {padding: 10vw}
  .appWrapper::before {background: rgba(0, 0, 0, 0.55)}
  .weatherIcon {max-width: 50px;}
  .app-container {align-items: flex-start;}
  .overview h1 {font-size: 32px; margin-bottom: 5px;}
  .overview p {font-size: 14px;}
  .current-temp {font-size: 40px; text-align: right;}
  .min-max-temp {font-size: 20px;}
}
